<template>
  <div class="ass_host mainboss">
    <div class="martop">
      <div class="topleft">
        <el-input
          v-model="search"
          placeholder=""
          @keyup.enter.native="gethostlist(1)"
          clearable
        ></el-input>
        <el-button type="primary" @click="gethostlist(1)"></el-button>
      </div>
      <div class="topright">
        <el-button type="primary" icon="el-icon-plus" @click="addhost"></el-button>
        <el-button type="primary" icon="el-icon-plus" @click="addbatch"></el-button>
        <el-button type="primary" icon="el-icon-delete" @click="batchdel"></el-button>
      </div>
    </div>
    <el-table
      :data="hostlist"
      :height="this.$store.state.tableHeight"
      resizable
      border
      style="width: 100%"
      v-loading="loading"
      element-loading-background="rgba(0, 0, 0, 0.5)"
      element-loading-text=""
      @selection-change="handleSelectionChange"
    >
      <el-table-column type="selection"></el-table-column>
      <el-table-column prop="name" align="center" sortable label=""></el-table-column>
      <el-table-column prop="ip" align="center" sortable label="IP"></el-table-column>
      <el-table-column prop="in_ip" align="center" sortable label="IP"></el-table-column>
      <el-table-column prop="idc_name" align="center" sortable label="IDC"></el-table-column>
      <el-table-column prop="user_name" align="center" sortable label=""></el-table-column>
      <el-table-column align="center" label="">
        <template slot-scope="scope">
          <el-switch
            v-model="scope.row.status"
            @change="changestatus(scope.row)"
            active-text
            inactive-text
          ></el-switch>
        </template>
      </el-table-column>
      <el-table-column fixed="right" label="" align="center" width="200">
        <template slot-scope="scope">
          <el-button
            :disabled="!scope.row.status"
            @click="handleedit(scope.row)"
            type="primary"
            size="mini"
            icon="el-icon-edit"
          ></el-button>
          <el-button
            :disabled="!scope.row.status"
            @click="handledel(scope.row)"
            type="primary"
            size="mini"
            icon="el-icon-edit"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <Pagein
      :total="total"
      :sizes="sizes"
      @handleCurrentChange="handleCurrentChange"
      @handleSizeChange="handleSizeChange"
    ></Pagein>

    <el-dialog
      title=""
      :visible.sync="AddDialogVisible"
      :modal-append-to-body="false"
      top="3%"
      width="30%"
      min-width="30%"
      center
    >
      <p class="prompt"></p>
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="name">
          <el-input v-model="ruleForm.name" clearable placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="IP" prop="IP">
          <el-input v-model="ruleForm.IP" clearable placeholder="IP"></el-input>
        </el-form-item>
        <el-form-item label="IP">
          <el-input v-model="ruleForm.publicIp" clearable placeholder="IP"></el-input>
        </el-form-item>
        <el-form-item label="IDC">
          <el-select v-model="ruleForm.IDC" placeholder="IDC">
            <el-option v-for="item in IDCs" :key="item.id" :label="item.label" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="management">
          <el-select v-model="ruleForm.management" placeholder="" @focus="getuserlist">
            <el-option
              v-for="item in userlist"
              :key="item.user_id"
              :label="item.username"
              :value="item.user_id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="ruleForm.label" placeholder=""  @focus="gettaglist">
            <el-option v-for="item in taglist" :key="item.id" :label="item.name" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="desc">
          <el-input type="textarea" v-model="ruleForm.desc" maxlength="255" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
          <el-button @click="resetForm('ruleForm')"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <el-dialog
      title=""
      :visible.sync="batchDialogVisible"
      :modal-append-to-body="false"
      top="3%"
      width="30%"
      min-width="30%"
      center
    >
      <p class="prompt">: [hostname] [ip] [port] [] , </p>
      <el-form ref="form" :model="form" label-width="0px">
        <el-form-item label>
          <el-input
            type="textarea"
            :autosize="{ minRows: 14, maxRows: 30}"
            placeholder="ops-hostname 10.0.0.1 22 ec2-user"
            v-model="form.textarea2"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('form')"></el-button>
          <el-button @click="resetForm('form')"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import Pagein from "../../public/pagein";
export default {
  name: "ass_host",
  data() {
    return {
      search: "",
      total: 0,
      page: 1,
      max_page: 3,
      loading: false,
      AddDialogVisible: false,
      batchDialogVisible: false,
      form: {
        textarea2: ""
      },
      actObj: {},
      sizes: [3, 4, 5],
      ruleForm: {
        name: "",
        IP: "",
        publicIp: "",
        port: "",
        IDC: "",
        region: "",
        management: "",
        label: "",
        desc: ""
      },
      rules: {
        name: [{ required: true, message: "", trigger: "blur" }],
        IP: [{ required: true, message: "IP", trigger: "blur" }],
        management: [
          { required: true, message: "", trigger: "change" }
        ]
      },
      taglist:[],
      userlist: [],
      IDCs: [
        {
          id: "001",
          label: "IDC1"
        },
        {
          id: "002",
          label: "IDC2"
        },
        {
          id: "003",
          label: "IDC3"
        }
      ],
      hostlist: [],
      multipleSelection: []
    };
  },
  components: {
    Pagein
  },
  methods: {
    gethostlist(val) {
      if (val == 1) {
        this.page = 1;
      }
      let _Url = "host/?page=" + this.page + "&max_page=" + this.max_page;
      this.loading = true;
      if (this.search) {
        _Url += "&search=" + this.search;
      }
      this.$http.get(_Url).then(res => {
        this.loading = false;
        this.total = res.data.data.count;
        this.hostlist = res.data.data.results;
      });
    },
    //
    handleSelectionChange(val) {
      this.multipleSelection = val;

      console.log("this.multipleSelection:", this.multipleSelection);
    },
    //
    addhost() {
      this.AddDialogVisible = true;
    },
    //
    addbatch() {
      this.batchDialogVisible = true;
    },
    //
    batchdel() {
      if (this.multipleSelection.length) {
        // console.log("multipleSelection:",this.multipleSelection)
        this.deletedata(1);
      } else {
        this.$message.error("");
      }
    },
    //
    getuserlist() {
      if (!this.userlist.length) {
        this.$http.get("user/").then(res => {
          console.log(res);
          if (res && res.status == 200) {
            if (res.data.status == 0) {
              this.userlist = res.data.data;
              console.log("userlist:",this.userlist)
            }
          }
        });
      }
    },
    //
    gettaglist(){
      if(!this.taglist.length){
        this.$http.get("tag/").then(res => {
          console.log(res);
          if (res && res.status == 200) {
            if (res.data.status == 0) {
              this.taglist = res.data.data;
              console.log("taglist:",this.taglist)
            }
          }
        });
      }
    },
    //
    changestatus(obj) {
      let _status = obj.status ? 1 : 0;
      let _parms = {
        status: _status
      };
      this.$http.patch("host/" + obj.id + "/", _parms).then(res => {
        if (res && res.status == 200) {
          if (res.data.status == 0) {
            this.$message.success("");
          }
        }
      });
    },
    //
    handleedit(obj) {
      this.AddDialogVisible = true;
    },
    //
    handledel(obj) {
      this.actObj = obj;
      this.deletedata(2);
    },
    //
    deletedata(val) {
      let arrid = [],
        _Url = "host/",
        _parms = {};
      if (val == 1) {
        //
        for (let i in this.multipleSelection) {
          arrid.push(this.multipleSelection[i].id);
        }
      } else if (val == 2) {
        //
        arrid.push(this.actObj.id);
      }
      _parms = {
        pk: arrid
      };

      this.$http.delete(_Url, { data: _parms }).then(res => {
        if (res && res.status == 200) {
          if (res.data.status == 0) {
            this.$message.success("");
            this.gethostlist();
          }
        }
      });
    },
    //
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          console.log(formName);
          if (formName == "form" && !this.form.textarea2) {
            this.$message.error("");
          } else {
            this.$message.success("");
            this.AddDialogVisible = false;
            this.batchDialogVisible = false;
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    //
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    //
    handleCurrentChange(val) {
      console.log("handleCurrentChange", val);
      this.page = val;
      this.gethostlist();
    },
    //
    handleSizeChange(val) {
      console.log("handleSizeChange", val);
      this.max_page = val;
      this.gethostlist();
    }
  },
  created() {
    this.gethostlist();
  }
};
</script>





<style lang="less" scoped>
.topleft {
  .el-input {
    width: 70%;
  }
}
.prompt {
  padding: 10px 0;
  text-align: center;
}
</style>


